.composition > div:first-child {
  margin-bottom: 40px;
}

.small {
  color: #868e96;
  font-size: 14px;
  margin-top: 10px;
}

.split {
  display: flex;
  justify-content: center;
}

.side-left,
.side-right {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 200px;
}

.side-left {
  background-color: #e7f5ff;
}

.side-right {
  background-color: #fff3bf;
}
